.conteudo {
 position: relative;

 display: flex;
 align-items: flex-start;
 justify-content: flex-end;
}

.background {
    height: 100vh;
    position: absolute;
    left: 0;
}

.info {
    width: 60%;
    margin-right: 90px;
    margin-top: 40px;
    color: #ffffff;
}

input {
  border: 1px solid #999999;
  border-radius: 22px;
  background: transparent;
  font-size: 18px;
  padding: 10px;
  min-width: 250px;
  margin-right: 12px;
  width: 80%;
  color: #ffffff;
}


Button {
    border: 1px solid #999999;
    border-radius: 22px;
    background: #2D333B;
    font-size: 18px;
    padding: 10px;
    margin-right: 12px;
    color: #ffffff
   
  }

  .perfil {
    display: flex;
    margin: 40px 0;
    
  }

  .profile {
    width: 120px;
    height: 120px;
    border-radius: 60px;
    border: 1px solid #e5e5e5; 
    margin: 20px;
    margin-right: 32px;
  }

  .perfil h3 {
    color:#999999;
    font-size: 24px;
    margin: 0;
  }

  .perfil span {
    display: block;
    color:#999999;
    font-size: 12px;
    margin: 0;
  }

  .perfil p {
    color:#999999;
    font-size: 12px;
    margin: 20px 0 0 0;
  }

  .repositorio {
    font-size: 32px;
    color: #ffffff;
    margin: 12px 0;
    text-align: center;
  }